.ck-content .webform-submission-form .form-item input,
.ck-content .webform-submission-form .form-item textarea {
  width: 100%;
  background: white;
  border: 2px solid #8E8C99;
  border-radius: 4px;
  padding: 12px 16px;
  outline: none;
  color: #000000;
  position: relative;
  transition: border 0.25s ease-out, box-shadow 0.25s ease-out;
}
.ck-content .webform-submission-form .form-item input:focus,
.ck-content .webform-submission-form .form-item textarea:focus {
  outline: none;
  border: 2px solid #000000;
  box-shadow: 0 0 0 8px rgba(142, 140, 153, 0.15);
  transition: border 0.125s ease-out, box-shadow 0.125s ease-out;
}
.ck-content .webform-submission-form .form-item + .form-item {
  margin: 16px 0 0;
}
.ck-content .webform-submission-form .form-submit {
  font-weight: 400;
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
  font-size: 14px;
  line-height: 1;
  text-align: center;
  background: #0057A8;
  color: #FFFFFF;
  min-width: 110px;
  padding: 16px 32px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 200;
  border: 0;
  border-radius: 24px;
  outline: 0;
  cursor: pointer;
  text-decoration: none;
  appearance: none;
  box-shadow: 0 0 0 6px rgba(142, 140, 153, 0);
  transition: all 0.25s ease-out;
}
.ck-content .webform-submission-form .form-submit:hover, .ck-content .webform-submission-form .form-submit:focus {
  background: #002855;
  transition: all 0.125s ease-out;
}
.ck-content .webform-submission-form .form-submit:focus {
  box-shadow: 0 0 0 6px rgba(142, 140, 153, 0.35);
  transition: all 0.125s ease-out;
}
.ck-content .webform-submission-form .form-submit:disabled {
  cursor: not-allowed;
  background: #8E8C99;
}
.ck-content .webform-submission-form .form-submit {
  margin-top: 32px;
  width: 100%;
}
.ck-content .store-ctas {
  display: flex;
  flex-direction: row;
}
.ck-content .store-cta + .store-cta {
  margin: 0 0 0 8px;
}
.ck-content .store-cta img {
  display: block;
  height: 37px;
}
.ck-content {
  background: #EBEBEB !important;
}
.ck-content h1, .ck-content h2, .ck-content h3,
.ck-content h4, .ck-content h5, .ck-content h6 {
  padding: 0 0 24px;
}